(function (w, d, f) {
	var defaultCheckString =
		'!"#$%&\'()*+,-./0123456789:;<=>?@ABCDEFGHIJKLMNOPQRSTUVWXYZ[\\]^_`abcdefghijklmnopqrstuvwxyz{|}~';
	var regex = /"/g;
	f = f.replace(regex, '"');

	var style = d.createElement('style');
	d.head.appendChild(style);
	style.sheet.insertRule(
		'@font-face{font-family:' +
			f +
			';src:url("data:font/opentype;base64,");}',
		0,
	);

	var element = d.createElement('span');
	element.style.fontSize = '1rem';
	isInstalledFont();

	/**
	 * 
	 * @param {string} [font] - 
	 * @param {string} [checkString] - 
	 * @returns {boolean}
	 */
	function isInstalledFont(font, checkString) {
		element.innerHTML = toString(checkString) || defaultCheckString;
		element.style.fontFamily =
			'"' + toString(font).replace(regex, '"') + '",' + f;
		d.body.appendChild(element);
		var result = element.offsetWidth > 1;
		d.body.removeChild(element);
		return result;
	}

	function toString(value) {
		if (value == null) return '';
		if (typeof value == 'string') return value;
		if (Array.isArray(value)) return value.map(toString) + '';
		var result = value + '';
		return '0' == result && 1 / value == -(1 / 0) ? '-0' : result;
	}

	w.isInstalledFont = isInstalledFont;
})(window, document, 'Blank');
